.ground-user {
  position: relative;
  margin: auto;
}
.ground-user .items-login {
  display: block;
  height: 40px;
  width: 110px;
  line-height: 40px;
  border-radius: 4px;
  background-color: #1890ff;
  color: #ffffff;
  font-size: 16px;
  text-transform: capitalize;
}
.ground-user .main-user {
  position: relative;
  display: block;
  text-align: center;
  justify-items: center;
  align-items: center;
}
.ground-user .profile-login {
  position: relative;
}
.ground-user .profile-login span {
  display: none;
}
.ground-user .profile-login .login {
  position: relative;
  display: block;
  cursor: pointer;
  z-index: 11;
}
/* -----------------login user--------- */
.ground-user .profile-login .show-login {
  position: fixed;
  width: 100%;
  height: 75px;
  background: #fdf9ff;
  left: 0;
  top: -300px;
  transition: all 0.3s ease-in-out;
  padding: 10px;
  grid-template-columns: 50% 50%;
  grid-gap: 5px;
  display: grid;
  text-align: center;
  justify-items: center;
  align-items: center;
  z-index: 103;
}

.ground-user .profile-login .show-login a {
  display: block;
  width: 120px;
  height: 35px;
  line-height: 35px;
  border-radius: 4px;
  background-color: rgb(24, 158, 255);
  color: #fdf9ff;
  font-size: 16px;
}
.ground-user .profile-login a {
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
  color: #302e4d;
}

/* ------------------ login------------------*/
.ground-user .profile-login .avatar-user {
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
  margin: auto;
}
.ground-user .profile-login .avatar-user img {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  border-radius: 50%;
}

@media screen and (max-width: 1000px) {
  .ground-user .items-login {
    display: none;
  }
  .ground-user .profile-login span {
    display: block;
  }
  .ground-user .profile-login .show-login.open {
    top: 0;
  }
  .ground-user {
    grid-column: 1/2;
    grid-row: 1/3;
  }

  .ground-card {
    justify-self: center;
  }
}
@media screen and (max-width: 420px) {
  .ground-user .profile-login .avatar-user {
    width: 30px;
    height: 30px;
  }
}
