.ground-search {
  display: block;
  position: relative;
  align-self: center;
  max-width: 1000px;
  margin: auto;
  width: 100%;
  left: 7px;
}
.ground-search span {
  display: none;
}
.ground-search .search form {
  display: flex;
  position: relative;
  transition: all 0.3s ease-in-out;
  margin: 0 auto;
  z-index: 11;
  border-radius: 5px;
}
/*----------input---------- */
.ground-search .search .input-search {
  width: 100%;
  position: relative;
  display: block;
}
.search .input-search .ant-form-item {
  margin: 0;
}
.ground-search .search .input-search input.keyword-search {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 35px;
  transition: 0.3s ease;
  padding: 5px 8px;
  border-bottom-left-radius: 2px;
  border-top-left-radius: 2px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  background: none;
  border: 1.1px solid #e8dfec;
  color: #464646;
  font-size: 14px;
  font-weight: 300;
  background: #fff;
}
.ground-search .from-search .search .input-search input::-webkit-input-placeholder {
  font-size: 13px;
}
.ground-search .from-search .search .input-search input:-ms-input-placeholder {
  font-size: 13px;
}
.ground-search .from-search .search .input-search input::placeholder {
  font-size: 13px;
}
.ground-search .ant-input:focus,
.ground-search .ant-input-focused {
  box-shadow: none;
}
/*----------icon input---------- */
.ground-search .search .icon-search {
  height: 35px;
  line-height: 35px;
  border-top: 1.1px solid #e8dfec;
  border-right: 1.1px solid #e8dfec;
  border-bottom: 1.1px solid #e8dfec;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  background: #05042c;
  width: 65px;
  text-align: center;
  position: relative;
  display: block;
}

.ground-search .search .icon-search button {
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
}
/* --------------Responsive--------------- */

@media screen and (max-width: 1000px) {
  .ground-search {
    display: block;
    justify-items: center;
    margin: auto;
    left: 0;
  }
  .ground-search span {
    display: block;
  }
  .ground-search .search {
    position: fixed;
    line-height: 75px;
    height: 75px;
    background-color: #fdf9ff;
    left: 0;
    top: -300px;
    padding: 20px 10px;
    width: 100%;
    transition: all 0.2s ease-in-out;
    z-index: 103;
  }
  .ground-search .search .icon-search {
    border-top: 1.1px solid #05042c;
    border-right: 1.1px solid #05042c;
    border-bottom: 1.1px solid #05042c;
  }

  .ground-search .search.open {
    top: 0;
  }
}

