.ground-user {
  position: relative;
  margin: auto;
}
.ground-user .items-login {
  display: block;
  height: 40px;
  width: 110px;
  line-height: 40px;
  border-radius: 4px;
  background-color: #1890ff;
  color: #ffffff;
  font-size: 16px;
  text-transform: capitalize;
}
.ground-user .main-user {
  position: relative;
  display: block;
  text-align: center;
  justify-items: center;
  align-items: center;
}
.ground-user .profile-login {
  position: relative;
}
.ground-user .profile-login span {
  display: none;
}
.ground-user .profile-login .login {
  position: relative;
  display: block;
  cursor: pointer;
  z-index: 11;
}
/* -----------------login user--------- */
.ground-user .profile-login .show-login {
  position: fixed;
  width: 100%;
  height: 75px;
  background: #fdf9ff;
  left: 0;
  top: -300px;
  transition: all 0.3s ease-in-out;
  padding: 10px;
  grid-template-columns: 50% 50%;
  grid-gap: 5px;
  display: grid;
  text-align: center;
  justify-items: center;
  align-items: center;
  z-index: 103;
}

.ground-user .profile-login .show-login a {
  display: block;
  width: 120px;
  height: 35px;
  line-height: 35px;
  border-radius: 4px;
  background-color: rgb(24, 158, 255);
  color: #fdf9ff;
  font-size: 16px;
}
.ground-user .profile-login a {
  font-size: 18px;
  font-weight: 500;
  text-transform: capitalize;
  color: #302e4d;
}

/* ------------------ login------------------*/
.ground-user .profile-login .avatar-user {
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
  margin: auto;
}
.ground-user .profile-login .avatar-user img {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  border-radius: 50%;
}

@media screen and (max-width: 1000px) {
  .ground-user .items-login {
    display: none;
  }
  .ground-user .profile-login span {
    display: block;
  }
  .ground-user .profile-login .show-login.open {
    top: 0;
  }
  .ground-user {
    grid-column: 1/2;
    grid-row: 1/3;
  }

  .ground-card {
    justify-self: center;
  }
}
@media screen and (max-width: 420px) {
  .ground-user .profile-login .avatar-user {
    width: 30px;
    height: 30px;
  }
}

.ground-header {
  background-color: #05042c;
  z-index: 104;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
}

.ground-header .main-header {
  display: grid;
  text-align: center;
  width: 100%;
  padding: 10px;
  grid-template-columns: 17% 70% 5% 8%;
  align-items: center;
  justify-content: center;
}
.main-item-card {
  justify-self: baseline;
}
.main-item-user {
  justify-self: center;
}

/* --------------------------------logo-------------------------------- */
.ground-header .main-header .main-item-logo {
  position: relative;
  max-width: 270px;
}
.ground-header .main-header .main-item-logo a {
  line-height: 50px;
  display: inline-block;
  position: relative;
  padding: 0 30px;

  transition: all 0.3s ease-in-out;
}
.ground-header .main-header .main-item-logo a::before,
.ground-header .main-header .main-item-logo a::after {
  position: absolute;
  content: '';
  height: 20px;
  width: 20px;
}
.ground-header .main-header .main-item-logo a::after {
  left: 0;
  top: 0;
  border-left: 4px solid #ec1839;
  border-top: 4px solid #ec1839;
}
.ground-header .main-header .main-item-logo a::before {
  border-right: 4px solid #ec1839;
  border-bottom: 4px solid #ec1839;
  right: 0;
  bottom: 0;
}
.ground-header .main-header .main-item-logo a img {
  width: 150px;
  height: auto;
}
/*  */
.ground-header .main-header .toggle-menu {
  display: none;
}
/* --------------Responsive--------------- */
@media screen and (max-width: 1300px) {
  .ground-header .main-header {
    grid-template-columns: 20% 60% 10% 10%;
  }
}
@media screen and (max-width: 1100px) {
  .ground-header .main-header {
    grid-template-columns: 25% 55% 5% 15%;
  }
}
@media screen and (max-width: 1000px) {
  .ground-header {
    z-index: 100;
  }
  .ground-header .main-header .main-item-logo a {
    padding: 0 10px;
  }
  .ground-header .main-header .main-item-logo a::after {
    border-left: 3px solid #ec1839;
    border-top: 3px solid #ec1839;
  }
  .ground-header .main-header .main-item-logo a::before {
    border-right: 3px solid #ec1839;
    border-bottom: 3px solid #ec1839;
  }
  .ground-header {
    padding-left: 0;
    box-shadow: 0 0.0533rem 0.0533rem 0 rgba(0, 0, 0, 0.1);
  }
  .ground-header .main-header .main-item-logo {
    display: block;
    position: relative;
    width: 100%;
    max-width: 220px;
  }
  .ground-header .main-header {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 10% 10% 60% 10% 10%;
    line-height: 0;
    padding: 10px 15px;
    grid-template-rows: 100%;
    grid-gap: 0;
  }
  .ground-header .main-header .toggle-menu {
    display: block;
  }
  .main-item-logo {
    grid-column: 3/4;
    grid-row: 1/3;
    justify-self: center;
  }
}
@media screen and (max-width: 350px) {
  .ground-header .main-header {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 12% 12% 52% 12% 12%;
